body {
  background: black;
  color: white;
  text-align: center;
  font-family: 'Poiret One'; }

.container__header {
  height: 100vh; }
  .container__header .name_block {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 100;
    text-align: right; }
    .container__header .name_block h1 {
      font-size: 4em;
      margin: 0.5em;
      display: inline-block;
      background: #000;
      padding: 0.25em; }
  .container__header .header_section {
    position: absolute;
    width: 100%;
    background-size: cover;
    height: 100vh; }
    .container__header .header_section p {
      margin: 0;
      padding: 0;
      font-size: 1.125em; }
    .container__header .header_section__developer {
      background: url(/static/laptop.jpeg) center;
      animation: left-bg-animation 1s;
      animation-timing-function: ease; }
      .container__header .header_section__developer__block {
        position: absolute;
        left: 0;
        top: 0;
        text-align: left;
        margin: 1em;
        background: #000;
        padding: 1em;
        z-index: 100; }
        .container__header .header_section__developer__block h2 {
          font-size: 8em;
          margin: 0; }
    .container__header .header_section__musician {
      background: url(/static/guitar.jpeg) center;
      clip-path: polygon(100% -20%, 21% 100%, 100% 100%);
      animation: right-bg-animation  1s;
      animation-timing-function: ease; }
      .container__header .header_section__musician__block {
        position: absolute;
        right: 0;
        bottom: 0;
        text-align: right;
        margin: 1em;
        background: #000;
        padding: 1em; }
        .container__header .header_section__musician__block h2 {
          font-size: 8em;
          margin: 0;
          margin-top: -0.3em; }

.container .info {
  margin-top: 2em; }
  .container .info__section {
    text-align: left;
    padding: 0.5em 2em; }
    .container .info__section__icon {
      width: 1em; }
    .container .info__section__list {
      margin: 0;
      padding: 0 1em;
      font-size: 2em;
      font-family: 'Montserrat', sans-serif; }
      .container .info__section__list li {
        list-style: none; }
    .container .info__section a {
      color: #fff; }
    .container .info__section h3 {
      margin: 0;
      font-size: 3.5em;
      padding-bottom: 0.5em; }
    .container .info__section p {
      font-family: 'Montserrat', sans-serif;
      font-size: 1.5em;
      margin: 0.5em 1em; }

.container footer {
  padding: 1em; }

@keyframes left-bg-animation {
  0% {
    background-position-x: 52%; }
  100% {
    background-position-x: 50%; } }

@keyframes right-bg-animation {
  0% {
    background-position-x: 48%; }
  100% {
    background-position-x: 50%; } }

@media (max-width: 1024px) {
  .container__header {
    height: auto; }
    .container__header .name_block {
      position: relative;
      text-align: center; }
      .container__header .name_block h1 {
        padding: 0; }
    .container__header .header_section {
      padding: 3em 0; }
      .container__header .header_section p {
        font-size: 2em; }
      .container__header .header_section__developer {
        position: relative;
        height: auto; }
        .container__header .header_section__developer__block {
          position: relative;
          text-align: center;
          font-size: 0.5em;
          display: inline-block;
          padding: 1em 1.5em; }
      .container__header .header_section__musician {
        position: relative;
        height: auto;
        clip-path: none; }
        .container__header .header_section__musician__block {
          position: relative;
          text-align: center;
          padding: 2em 0;
          font-size: 0.5em;
          display: inline-block;
          padding: 1em 1.5em; }
  .info {
    font-size: 0.6em; } }
